<template>
  <div>
    <img
      class='buff'
      v-for='(buff, key) in buffList'
      :key='key'
      :src='`/img/icons/${buff.icon}.jpg`'
      v-tooltip.bottom-start='buff.message'
    />
  </div>
</template>


<script>
export default {
  props: ["buffs"],
  computed: {
    buffList() {
      const buffList = [];
      for (const key in this.buffs) {
        if (key === "invulnerability" && this.buffs[key][18]) {
          buffList.push({
            icon: "any",
            message: "This character is invulnerable",
          });
        }
      }
      return buffList;
    },
  },
};
</script>

<style lang="scss">
.buff {
  height: 30px;
  border-radius: 50%;
  margin-right: 2px;
  box-shadow: 1px 1px 2px 2px rgba($color: #000000, $alpha: 0.2);
}
</style>